import {Component} from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {Broadcast} from '../../shared/models/broadcast';
import {Missionary} from '../../shared/models/missionary';
import {Announcement} from "../../shared/models/announcement";
import {AnnouncementService} from "../../shared/data/announcement.service";
import {DATA} from "../../shared/data/data";
import {map} from "rxjs/operators";

@Component({
  selector: 'lehi41-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent {
  data = DATA;

  announcements$: BehaviorSubject<Announcement[]> = new BehaviorSubject<Announcement[]>([]);
  missionaries$: BehaviorSubject<Missionary[]> = new BehaviorSubject<Missionary[]>(this.data.missionaries);
  broadcastLinks$: BehaviorSubject<Broadcast[]> = new BehaviorSubject<Broadcast[]>(this.data.broadcastLinks.filter((z) => z.active));

  constructor(
    private announcementService: AnnouncementService
  ) {
    this.announcementService.getAll()
      .pipe(
        map((announcements: Announcement[]) => {
          announcements.forEach(a => {
            a.date = new Date(a.date)
          });
          return announcements.sort((b, a) => new Date(b.date).getTime() - new Date(a.date).getTime());
        })
      )
      .subscribe(r => this.announcements$.next(r));
  }
}
